import React from "react";

export default class Search extends React.Component {
  state = {
    search: 'panda',
    type: 'all',
  };

  handleMovie = (e) => {
    if (e.key === "Enter") {
      this.props.getMovie(this.state.search, this.state.type);
    }
  };

  handleFillter = (e) => {
      this.setState(() => ({type: e.target.dataset.type}), () => {
            this.props.getMovie(this.state.search, this.state.type)
      })
  }
 
  render() {
    return (
      <div class="row">
        <div class="col s12">
          <div class="input-field">
            <input
              id="email_inline"
              type="search"
              class="validate"
              placeholder="Search"
              value={this.state.search}
              onChange={(e) => {
                this.setState({ search: e.target.value });
              }}
              onKeyDown={this.handleMovie}
              checked={this.state.type === 'all'}
            />
            <button
              className="btn search-btn"
              onClick={() => this.props.getMovie(this.state.search, this.state.type)}
            >
              Search Movie
            </button>
          </div>
          <div>
            <label>
              <input className="with-gap" name="type" type="radio" data-type="all" onChange={this.handleFillter} checked={this.state.type === 'all'}/>
              <span>All</span>
            </label>
            <label>
              <input className="with-gap" name="type" type="radio" data-type="movies" onChange={this.handleFillter} checked={this.state.type === 'movies'} />
              <span>Movies only</span>
            </label>
            <label>
              <input className="with-gap" name="type" type="radio" data-type="series" onChange={this.handleFillter} checked={this.state.type === 'series'} />
              <span>Series only</span>
            </label>
          </div>
        </div>
      </div>
    );
  }
}
