import React from "react"
import Loader from "../components/Loader"
import Movies from "../components/Movies"
import Search from "../components/Search"

export default class Main extends React.Component {
    state = {
        movies: [],
        loading: true,
    }

    componentDidMount () {
        fetch('http://www.omdbapi.com/?i=tt3896198&apikey=24510d3c&s=panda')
            .then(data => data.json())
            .then(data => this.setState({movies: data.Search, loading: false}))
    }

    getMovie = (str, type = 'all') => {
        this.setState({loading: true})
        fetch(`http://www.omdbapi.com/?i=tt3896198&apikey=24510d3c&s=${str}${type !== 'all' ? `&type=${type}` : '' }`)
            .then(data => data.json())
            .then(data => this.setState({movies: data.Search, loading: false}))
    }

    render () {
        return (
            <div className="container content">
                <Search getMovie={this.getMovie} />
                {this.state.loading ? <Loader />  : <Movies movies={this.state.movies} />}
            </div>
        )
    }
}